import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectIsLoadingAuth } from '@store/auth/auth.selectors';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { SignupValidators } from '@utils/signup-validators';
import { URLS } from '@enums/urls';
import { IChangePasswordData } from '@ApiClient/backend/Login/changePassword';
import { changePasswordAction } from '@store/settings/settings.actions';

@Component({
  selector: 'app-change-password',
  templateUrl: 'change-password.component.html',
  styleUrls: ['change-password.component.scss'],
})
export class ChangePasswordComponent {
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingAuth);

  changePasswordForm = new FormGroup(
    {
      oldPassword: new FormControl('', [Validators.required]),
      newPassword: new FormControl('', [Validators.required, Validators.minLength(8)]),
      confirmPassword: new FormControl('', [Validators.required]),
    },
    [SignupValidators.passwordMatch('newPassword', 'confirmPassword')],
  );

  onSubmit() {
    if (this.changePasswordForm.invalid) {
      this.changePasswordForm.markAllAsTouched();
      return;
    }
    const changePasswordData: IChangePasswordData = {
      password: this.changePasswordForm.get('newPassword')?.value,
      actualPassword: this.changePasswordForm.get('oldPassword')?.value,
    } as IChangePasswordData;
    this.store.dispatch(changePasswordAction({ changePasswordData }));
  }

  constructor(private store: Store<AppStateInterface>) {}

  protected readonly URLS = URLS;
}
