import { createSelector } from '@ngrx/store';

import { AppStateInterface } from '@store/app/app-state.interface';
import { SettingsStateInterface } from '@store/settings/settingsState.interface';

export const settingsFeatureSelector = (state: AppStateInterface): SettingsStateInterface => state.settings;

export const selectIsLoadingSettings = createSelector(settingsFeatureSelector, (state: SettingsStateInterface) => state.isLoading);

export const selectEmail = createSelector(settingsFeatureSelector, (state: SettingsStateInterface) => state.email);

export const selectIdentity = createSelector(settingsFeatureSelector, (state: SettingsStateInterface) => {
  return {
    firstName: state.firstName,
    lastName: state.lastName,
    birthDate: state.birthDate,
  };
});
