import { ImageComponent } from '@ApiClient/components/image/image.component';
import { Component, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { User } from '@shared-types/user.interface';
import { Store } from '@ngrx/store';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCurrentUser } from '@store/auth/auth.selectors';

import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-photo-edit-profile',
  standalone: true,
  imports: [ImageComponent, CommonModule],
  templateUrl: './photo-edit-profile.component.html',
  styleUrl: './photo-edit-profile.component.scss',
})
export class PhotoEditProfileComponent {
  @ViewChild('photoProfile') profilePhoto!: ImageComponent;
  currentUser$: Observable<User | null> = this.store.select(selectCurrentUser);
  imageId: string | null = '';

  constructor(private store: Store<AppStateInterface>) {
    this.currentUser$.subscribe((user) => {
      this.imageId = user ? user.pictureId : null;
    });
  }

  onRemovePhoto() {
    this.profilePhoto.idPicture = '';
    this.profilePhoto.reset();
  }
}
